import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Table } from 'react-bootstrap';


export default  function TableOwner() {
      const [owner, setOwner] = useState([])

      useEffect(() =>{
      axios({
        method: "GET",
        url: "http://127.0.0.1:8000/api/v1/owner/"
      }).then(response => {
        setOwner(response.data)
      })
      }, [])

    return (
    <div className="container">
        <Table striped bordered hover size="sm">
          <thead>
            <tr>
              <th>Owner</th>
              <th>Description</th>
            </tr>
          </thead>
          <tbody>
          {owner.map(o => (
            <tr key={o.id}>
              <td><Link to={{ pathname: `/owner/${o.id}`, fromDashboard: false }}>{o.surname} {o.name[0]}</Link></td>
              <td>{o.owner_description}</td>
            </tr>
          ))}
          </tbody>
        </Table>
    </div>
    )
}